export const scalarParse = (s?: string): Scalar | undefined => {
  if (s === 'undefined') {
    return undefined;
  }
  if (s === 'null') {
    return null;
  }
  if (s === 'true') {
    return true;
  }
  if (s === 'false') {
    return false;
  }
  if (s === '0') {
    return 0;
  }
  if (s === '') {
    return '';
  }
  if (s && '1234567890+-'.includes(s[0]) && s.length <= 17) {
    const num = +s;
    return isNaN(num) ? s : num;
  }
  return s;
};
