import { scalarParse } from './scalar';

const RE_ATTRS = /([a-z][a-zA-Z0-9-:]+)(="[^"]*"|={[^}]*})?/g;
const RE_EMPTY = /^\s*$/;
const RE_XML_COMMENT = /<!--((?!-->)[\s\S])*-->/g;
const RE_XML_TAG = /(<)(\/?)([a-zÐ°-Ñ][a-zÐ°-Ñ0-9-:]*)((?:\s+[a-z][a-z0-9-:]+(?:="[^"]*"|={[^}]*})?)*)\s*(\/?)>/gi;
const SINGLE_TAGS = { img: 1, input: 1, br: 1, col: 1 };

const RE_XML_ENTITY = /&#?[0-9a-z]{3,5};/g;

const SUBST_XML_ENTITY: Record<string, string> = {
  amp: '&',
  gt: '>',
  lt: '<',
  quot: '"',
  nbsp: ' ',
};

const ESCAPE_XML_ENTITY: Record<string, string> = {
  34: '&quot;',
  38: '&amp;',
  39: '&#39;',
  60: '&lt;',
  62: '&gt;',
};
const RE_ESCAPE_XML_ENTITY = /["'&<>]/g;

const FN_ESCAPE_XML_ENTITY = (m: string) => ESCAPE_XML_ENTITY[m.charCodeAt(0)];

const FN_XML_ENTITY = function (s0: string) {
  const s = s0.substring(1, s0.length - 1);
  return s[0] === '#' ? String.fromCharCode(+s.slice(1)) : SUBST_XML_ENTITY[s] || ' ';
};

export const skipQoutes = (s: string) => (s[0] === '"' && s[s.length - 1] === '"' ? s.slice(1, -1) : s);

export const decodeXmlEntities = (s = '') => s.replace(RE_XML_ENTITY, FN_XML_ENTITY);

const addNode = (c: XmlNode, tag: string) => {
  const node: XmlNode = { tag };
  (c.nodes || (c.nodes = [])).push(node);

  return node;
};

const parseAttrs = (node: XmlNode, s: string) => {
  if (!s) return;
  node.attrs = {};
  for (let e = RE_ATTRS.exec(s); e; e = RE_ATTRS.exec(s)) {
    const value = !e[2] ? true : scalarParse(decodeXmlEntities(skipQoutes(e[2].slice(1))));
    node.attrs[e[1]] = value;
  }
};

export const escapeXml = (s: string) => (!s ? '' : ('' + s).replace(RE_ESCAPE_XML_ENTITY, FN_ESCAPE_XML_ENTITY));

export const attributesToString = (v?: Hash): string | undefined => {
  if (typeof v === 'object') {
    if (Array.isArray(v)) {
      return !v.length ? '[]' : '[' + attributesToString(v[0]) + ' x' + v.length + ']';
    }
    return '{' + Object.keys(v).toString() + '}';
  }
  return v;
};

export function xmlStringify({ tag, attrs, nodes = [] }: XmlNode, tab = ''): string {
  if (tag === '#text') {
    return `${tab}${escapeXml(String(attrs?.text || ''))}`;
  }
  const sattrs = attributesToString(attrs);
  const ssubs = nodes.map((c) => xmlStringify(c, `  ${tab}`)).join('\n');

  return `${tab}<${tag}${sattrs}` + (!ssubs ? '/>' : `>\n${ssubs}\n${tab}</${tag}>`);
}

export const xmlParse = (_s?: string): XmlNode[] => {
  const ctx: XmlNode[] = [{ tag: '#root' }];
  const s = String(_s).trim().replace(RE_XML_COMMENT, '');
  let lastIndex = 0;
  // head text omitted
  for (let e = RE_XML_TAG.exec(s); e; e = RE_XML_TAG.exec(s)) {
    // preceding text
    const text = e.index && s.slice(lastIndex, e.index);
    if (text && !text.match(RE_EMPTY)) {
      const node = addNode(ctx[0], '#text');
      node.attrs = { text: decodeXmlEntities(text) };
    }
    // closing tag
    if (e[2]) {
      if (ctx[0].tag !== e[3]) {
        throw new Error(
          ' XML Parse closing tag does not match at: ' +
            e.index +
            ' near ' +
            e.input.slice(Math.max(e.index - 150, 0), e.index) +
            '^^^^' +
            e.input.slice(e.index, Math.min(e.index + 150, e.input.length))
        );
      }
      ctx.shift();
    } else {
      const elt = addNode(ctx[0], e[3]);
      parseAttrs(elt, e[4].trim());
      // not single tag
      if (!(e[5] || e[3] in SINGLE_TAGS)) {
        ctx.unshift(elt);
        if (ctx.length === 1) {
          throw new Error('Parse error at: ' + e[0]);
        }
      }
    }
    lastIndex = RE_XML_TAG.lastIndex;
  }
  // tail text omitted
  return ctx[0].nodes || [];
};
