/**
 * Parses string into Url object.
 */
export const urlParse = (_s: string): Url => {
  const r: Url = { path: [], params: {}, host: '' };
  if (!_s) {
    return r;
  }
  let p: number;
  let s = _s;
  // extract type:
  p = s.indexOf(':');
  if (p > -1) {
    r.protocol = s.slice(0, p);
    s = s.slice(p + 1);
  }
  // extract hash:
  p = s.indexOf('#');
  if (p > -1) {
    r.hash = decodeURIComponent(s.slice(p + 1));
    s = s.slice(0, p);
  }
  // extract query params:
  p = s.indexOf('?');
  if (p > -1) {
    s.slice(p + 1)
      .replace(/\+/g, ' ')
      .split('&')
      .forEach((param) => {
        const [key, value] = param.split('=');
        if (value) {
          r.params[decodeURIComponent(key)] = decodeURIComponent(value);
        }
      });
    s = s.slice(0, p);
  }
  // host and path:
  r.path = s.split('/').map(decodeURIComponent);
  const path = r.path;
  if (path.length > 2 && !path[0] && !path[1]) {
    path.shift();
    path.shift();
    r.host = path.shift() || '';
  }

  return r;
};

/**
 *  Represents an Url object as a string
 *
 * @param {object} r Url object like `{type, host, path, params, hash }`
 * @returns string in format `type:host/path?params#hash`
 */
export const urlStringify = (r: Url): string => {
  let result = '';
  if (!r) {
    return result;
  }
  if (r.host) {
    if (r.protocol) {
      result += `${r.protocol}:`;
    }
    result += `//${r.host}`;
  }
  if (r?.path.length) {
    result += `/${r.path.map(encodeURIComponent).join('/')}`;
  }
  const { params } = r;

  const keys = Object.keys(params).filter((key) => params[key] != null);
  if (keys.length) {
    result += `?${keys
      .map((key) => `${encodeURIComponent(key)}=${encodeURIComponent(params[key])}`)
      .join('&')}`.replace(/ /g, '+');
  }

  if (r.hash) {
    result += `${r.hash[0] === '#' ? '' : '#'}${encodeURIComponent(r.hash)}`;
  }

  return result;
};

export const urlAppendParams = (url: string = '', params: UrlParams = {}): string => {
  const queryString = Object.entries(params)
    ?.map(([key, value]) => `${key}=${encodeURIComponent(value)}`)
    .join('&');

  return [url, queryString].filter(Boolean).join(url.includes('?') ? '&' : '?');
};
