export const showCounts = (counts: Obj, nicNema = 'â­•', neZnojdzeno = 'â­•') => {
  if (!counts) {
    return 'â€¦';
  }
  const { total, actual } = counts;
  if (!total) {
    return nicNema;
  }
  if (!actual) {
    return neZnojdzeno;
  }
  if (actual === total) {
    return total;
  }
  return actual + ' / ' + total;
};
